.topbar{
	width:100%;
	height:50px;
	background-color: white;
	position: sticky;
	top: 0;
	z-index: 999;
}

.topbarWrapper{
	height : 100%;
	display: flex;
	padding : 0px 20px;
	align-items: center;
	justify-content: space-between;

}


.logo{
	font-weight: bold;
	font-size: 30px;
	color:red;
	cursor: pointer;

}

.topRight{
	display: flex;
	align-items: center;
	color: #555;
}

.topbarIconContainer{
	position: relative;
	cursor: pointer;
	margin-right: 10px;
}

.topIconBadge{
	width: 15px;
	height: 15px;
	position: absolute;
	top: -4px;
	right: -4px;
	background-color: red;
	color: white;
	border-radius: 50%;
	display: flex;
	align-items: center;
	justify-content: center;
	font-size: 10px;
	font-weight: 600;
	padding: 1px;

}


.topAvatar{
	width: 40px;
	height: 40px;
	border-radius:50%;
	cursor: pointer;
	object-fit: cover;
	


}